<template>
  <div class="fast-widget overview-widget" :id="containerId" ref="container">
    <div v-if="!loaded && !error" class="fast-loading">
      <div class="loading-spinner"></div>
      <span>Loading Technical Analysis...</span>
    </div>
    
    <div v-if="error" class="fast-error">
      <span> Failed to load</span>
      <button @click="retry" class="retry-btn">Retry</button>
    </div>

    <!-- TradingView Widget Container -->
    <div v-show="loaded" class="tradingview-widget-container">
      <div class="tradingview-widget-container__widget"></div>
    </div>
  </div>
</template>

<script>
import { widgetLoadManager } from '@/utils/widgetLoadManager'

export default {
  name: 'TechnicalAnalysisWidget',
  props: {
    symbol: {
      type: String,
      required: true
    },
    exchange: {
      type: String,
      default: 'NASDAQ'
    },
    priority: {
      type: Number,
      default: 2
    }
  },
  data() {
    return {
      loaded: false,
      error: false,
      loadStartTime: 0,
      isVisible: false,
      observer: null,
      retryCount: 0
    }
  },
  computed: {
    containerId() {
      return `technical-analysis-${this.symbol}-${Date.now()}`
    }
  },
  async mounted() {
    this.setupIntersectionObserver()
  },
  beforeUnmount() {
    if (this.observer) {
      this.observer.disconnect()
    }
  },
  methods: {
    setupIntersectionObserver() {
      //  rootMargin
      const rootMargins = {
        1: '300px', //  300px 
        2: '150px', //  150px 
        3: '50px'   //  50px 
      }

      this.observer = new IntersectionObserver(
        (entries) => {
          entries.forEach((entry) => {
            if (entry.isIntersecting && !this.loaded && !this.error) {
              this.isVisible = true
              this.loadWidget()
              this.observer.disconnect() // 
            }
          })
        },
        {
          rootMargin: rootMargins[this.priority] || '150px',
          threshold: 0.1
        }
      )

      this.observer.observe(this.$refs.container)
    },

    async loadWidget() {
      this.loadStartTime = performance.now()
      this.loaded = false
      this.error = false

      try {
        // 
        const delays = {
          1: 0,     // 
          2: 300,   //  300ms
          3: 600    //  600ms
        }

        const delay = delays[this.priority] || 0
        if (delay > 0) {
          await new Promise(resolve => setTimeout(resolve, delay))
        }

        //  Widget Load Manager 
        const widgetId = `technical-analysis-${this.symbol}-${this.containerId}`
        await widgetLoadManager.addToQueue(
          () => this.createWidget(),
          widgetId,
          this.priority
        )

      } catch (err) {
        console.error('Failed to load Technical Analysis widget:', err)
        this.error = true
      }
    },

    async createWidget() {
      return new Promise((resolve, reject) => {
        this.$nextTick(async () => {
          const container = this.$refs.container
          if (!container) {
            reject(new Error('Container not found'))
            return
          }

          const config = this.getWidgetConfig()
          const scriptUrl = 'https://s3.tradingview.com/external-embedding/embed-widget-technical-analysis.js'
          
          const script = document.createElement('script')
          
          script.type = 'text/javascript'
          script.src = scriptUrl
          script.async = true
          script.innerHTML = JSON.stringify(config)
          
          //  timeout - 
          const baseTimeouts = {
            1: 8000,   // 8
            2: 12000,  // 12
            3: 15000   // 15
          }
          
          const retryCount = this.retryCount || 0
          const timeoutDuration = baseTimeouts[this.priority] + (retryCount * 3000) //  3 
          
          const timeout = setTimeout(() => {
            this.error = true
            reject(new Error(`Widget load timeout after ${timeoutDuration}ms (retry: ${retryCount})`))
          }, timeoutDuration)
          
          script.onload = () => {
            clearTimeout(timeout)
            this.loaded = true
            
            const loadTime = performance.now() - this.loadStartTime
            console.log(`Technical Analysis widget ${this.symbol} loaded in ${loadTime.toFixed(2)}ms (retry: ${retryCount})`)
            
            resolve()
          }
          
          script.onerror = () => {
            clearTimeout(timeout)
            this.error = true
            reject(new Error('Script load failed'))
          }
          
          //  script  tradingview-widget-container__widget 
          const widgetContainer = container.querySelector('.tradingview-widget-container__widget')
          if (widgetContainer) {
            widgetContainer.appendChild(script)
          } else {
            container.appendChild(script)
          }
        })
      })
    },

    getWidgetConfig() {
      return {
        "colorTheme": "light",
        "displayMode": "single",
        "isTransparent": true,
        "locale": "en",
        "interval": "1D",
        "disableInterval": false,
        "width": "100%",
        "height": "100%",
        "symbol": `${this.exchange}:${this.symbol}`,
        "showIntervalTabs": true
      }
    },

    async retry() {
      // 
      this.retryCount = (this.retryCount || 0) + 1
      const maxRetries = 2
      
      if (this.retryCount > maxRetries) {
        console.error(`Technical Analysis widget ${this.symbol} exceeded max retries (${maxRetries})`)
        return
      }
      
      // 
      const backoffDelay = Math.min(1000 * Math.pow(2, this.retryCount - 1), 5000) //  5 
      console.log(`Technical Analysis widget ${this.symbol} retrying in ${backoffDelay}ms (attempt ${this.retryCount}/${maxRetries})`)
      
      await new Promise(resolve => setTimeout(resolve, backoffDelay))
      
      this.setupIntersectionObserver()
    }
  }
}
</script>

<style scoped>
.fast-widget {
  width: 100%;
  height: 100%;
  min-height: 500px;
  position: relative;
}

/*  Symbol Overview  */
.fast-widget.overview-widget {
  background: #ffffff;
  padding: 8px;
  box-sizing: border-box;
}

.fast-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-height: 500px;
  background: #f8f9fa;
  border-radius: 8px;
  color: #6c757d;
}

/*  */
.fast-widget.overview-widget .fast-loading {
  margin: -8px; /*  padding */
}

.loading-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #e9ecef;
  border-top: 3px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.fast-loading span {
  font-size: 0.9rem;
  font-weight: 500;
}

.fast-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-height: 500px;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 8px;
  color: #721c24;
}

/*  */
.fast-widget.overview-widget .fast-error {
  margin: -8px; /*  padding */
}

.retry-btn {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 500;
}

.retry-btn:hover {
  background: #c82333;
}

/* TradingView Widget  */
.tradingview-widget-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.tradingview-widget-container__widget {
  width: 100%;
  height: 100%; /*  copyright  */
}
</style>