<template>
  <div 
    class="lazy-widget" 
    :id="containerId" 
    ref="container"
    :style="{ height: height }"
  >
    <!--  -->
    <div v-if="!isVisible && !loaded" class="widget-placeholder">
      <div class="placeholder-content">
        <div class="placeholder-icon"></div>
        <div class="placeholder-text">{{ widgetType }} Widget</div>
        <div class="placeholder-subtext">Scroll to load</div>
      </div>
    </div>

    <!--  -->
    <div v-if="isVisible && !loaded && !error" class="widget-loading">
      <div class="loading-spinner"></div>
      <span>Loading {{ widgetType }}...</span>
    </div>
    
    <!--  -->
    <div v-if="error" class="widget-error">
      <span> Failed to load</span>
      <button @click="retry" class="retry-btn">Retry</button>
    </div>
  </div>
</template>

<script>
import { widgetLoadManager } from '@/utils/widgetLoadManager'

export default {
  name: 'LazyTradingViewWidget',
  props: {
    widgetType: {
      type: String,
      required: true
    },
    config: {
      type: Object,
      required: true
    },
    scriptUrl: {
      type: String,
      required: true
    },
    height: {
      type: String,
      default: '400px'
    },
    priority: {
      type: Number,
      default: 1 // 1=, 2=, 3=
    }
  },
  data() {
    return {
      isVisible: false,
      loaded: false,
      error: false,
      observer: null,
      loadStartTime: 0
    }
  },
  computed: {
    containerId() {
      return `lazy-widget-${this.widgetType}-${Date.now()}`
    }
  },
  mounted() {
    this.setupIntersectionObserver()
  },
  beforeUnmount() {
    if (this.observer) {
      this.observer.disconnect()
    }
  },
  methods: {
    setupIntersectionObserver() {
      //  rootMargin
      const rootMargins = {
        1: '200px', //  200px 
        2: '100px', //  100px 
        3: '50px'   //  50px 
      }

      this.observer = new IntersectionObserver(
        (entries) => {
          entries.forEach((entry) => {
            if (entry.isIntersecting && !this.loaded && !this.error) {
              this.isVisible = true
              this.loadWidget()
              this.observer.disconnect() // 
            }
          })
        },
        {
          rootMargin: rootMargins[this.priority] || '100px',
          threshold: 0.1
        }
      )

      this.observer.observe(this.$refs.container)
    },

    async loadWidget() {
      this.loadStartTime = performance.now()
      
      try {
        // 
        const delays = {
          1: 0,     // 
          2: 500,   //  500ms
          3: 1000   //  1000ms
        }

        const delay = delays[this.priority] || 0
        if (delay > 0) {
          await new Promise(resolve => setTimeout(resolve, delay))
        }

        //  Widget Load Manager 
        const widgetId = `lazy-${this.widgetType}-${this.containerId}`
        await widgetLoadManager.addToQueue(
          () => this.createWidget(),
          widgetId,
          this.priority
        )
      } catch (err) {
        console.error('Failed to load widget:', err)
        this.error = true
      }
    },

    async createWidget() {
      return new Promise((resolve, reject) => {
        this.$nextTick(() => {
          const container = this.$refs.container
          if (!container) {
            reject(new Error('Container not found'))
            return
          }

          // 
          const existingContent = container.querySelector('.widget-placeholder, .widget-loading')
          if (existingContent) {
            existingContent.style.display = 'none'
          }

          try {
            //  TradingView 
            const widgetContainer = document.createElement('div')
            widgetContainer.className = 'tradingview-widget-container'
            widgetContainer.style.height = '100%'
            widgetContainer.style.width = '100%'

            const widgetContent = document.createElement('div')
            widgetContent.className = 'tradingview-widget-container__widget'
            widgetContent.style.height = '100%'
            widgetContent.style.width = '100%'

            widgetContainer.appendChild(widgetContent)

            //  TradingView script - 
            const script = document.createElement('script')
            script.type = 'text/javascript'
            script.src = this.scriptUrl
            script.async = true
            
            //  script 
            const configJson = JSON.stringify(this.config)
            script.innerHTML = configJson
            
            // 
            console.log(` Creating ${this.widgetType} widget with config:`, this.config)
            console.log(` Config JSON:`, configJson)
            
            //  - 
            const timeouts = {
              1: 8000,  // 8
              2: 10000, // 10
              3: 12000  // 12
            }
            
            const timeout = setTimeout(() => {
              console.warn(`${this.widgetType} widget load timeout`)
              this.error = true
              reject(new Error('Widget load timeout'))
            }, timeouts[this.priority] || 10000)
            
            script.onload = () => {
              clearTimeout(timeout)
              this.loaded = true
              
              const loadTime = performance.now() - this.loadStartTime
              console.log(` ${this.widgetType} widget loaded in ${loadTime.toFixed(2)}ms (Priority: ${this.priority})`)
              
              resolve()
            }
            
            script.onerror = (error) => {
              clearTimeout(timeout)
              console.error(` ${this.widgetType} script load failed:`, error)
              this.error = true
              reject(new Error('Script load failed'))
            }
            
            //  DOM
            widgetContent.appendChild(script)
            container.appendChild(widgetContainer)
            
          } catch (error) {
            console.error(` ${this.widgetType} widget creation failed:`, error)
            this.error = true
            reject(error)
          }
        })
      })
    },

    async retry() {
      this.error = false
      this.loaded = false
      await this.loadWidget()
    }
  }
}
</script>

<style scoped>
.lazy-widget {
  width: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}

/*  */
.widget-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  border: 2px dashed #dee2e6;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.widget-placeholder:hover {
  background: linear-gradient(135deg, #e9ecef 0%, #dee2e6 100%);
  border-color: #adb5bd;
}

.placeholder-content {
  text-align: center;
  color: #6c757d;
}

.placeholder-icon {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.placeholder-text {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.placeholder-subtext {
  font-size: 0.8rem;
  opacity: 0.7;
}

/*  */
.widget-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: #f8f9fa;
  border-radius: 8px;
  color: #6c757d;
}

.loading-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #e9ecef;
  border-top: 3px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.widget-loading span {
  font-size: 0.9rem;
  font-weight: 500;
}

/*  */
.widget-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 8px;
  color: #721c24;
}

.retry-btn {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 500;
  transition: background-color 0.2s;
}

.retry-btn:hover {
  background: #c82333;
}

/* TradingView  */
:global(.lazy-widget .tradingview-widget-container) {
  width: 100% !important;
  height: 100% !important;
  overflow: hidden !important;
}

:global(.lazy-widget .tradingview-widget-container__widget) {
  width: 100% !important;
  height: 100% !important;
}
</style>