<template>
  <div class="vix-widget-container" ref="vixContainer" :style="{ height: '600px' }">
    <div v-if="loading" class="loading-state">
      <div class="loading-spinner"></div>
      <span>Loading VIX Index...</span>
    </div>
    <div v-if="error" class="error-state">
      <span> Failed to load VIX</span>
      <button @click="loadVixWidget" class="retry-btn">Retry</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VixWidget',
  data() {
    return {
      loading: true,
      error: false,
      widgetId: `vix-widget-${Date.now()}`
    }
  },
  mounted() {
    this.loadVixWidget()
  },
  methods: {
    async loadVixWidget() {
      this.loading = true
      this.error = false
      
      try {
        // 
        const container = this.$refs.vixContainer
        if (!container) return
        
        // 
        while (container.firstChild) {
          container.removeChild(container.firstChild)
        }
        
        console.log(' Starting VIX widget creation with FRED:VIXCLS...')
        
        //  LazyTradingViewWidget 
        await this.createWidget()
        
      } catch (error) {
        console.error(' VIX widget creation failed:', error)
        this.error = true
        this.loading = false
      }
    },

    async createWidget() {
      return new Promise((resolve, reject) => {
        this.$nextTick(() => {
          const container = this.$refs.vixContainer
          if (!container) {
            reject(new Error('Container not found'))
            return
          }

          try {
            //  TradingView  -  LazyTradingViewWidget
            const widgetContainer = document.createElement('div')
            widgetContainer.className = 'tradingview-widget-container'
            widgetContainer.style.height = '100%'
            widgetContainer.style.width = '100%'

            const widgetContent = document.createElement('div')
            widgetContent.className = 'tradingview-widget-container__widget'
            widgetContent.style.height = '100%'
            widgetContent.style.width = '100%'

            widgetContainer.appendChild(widgetContent)

            //  script  -  LazyTradingViewWidget 
            const script = document.createElement('script')
            script.type = 'text/javascript'
            script.src = 'https://s3.tradingview.com/external-embedding/embed-widget-symbol-overview.js'
            script.async = true
            
            // VIX  -  FRED:VIXCLS Market Daily Insight 
            const vixConfig = {
              "lineWidth": 2,
              "lineType": 2,
              "chartType": "line",
              "showVolume": false,
              "fontColor": "rgb(106, 109, 120)",
              "gridLineColor": "rgba(46, 46, 46, 0.06)",
              "volumeUpColor": "rgba(34, 171, 148, 0.5)",
              "volumeDownColor": "rgba(247, 82, 95, 0.5)",
              "backgroundColor": "#ffffff",
              "widgetFontColor": "#0F0F0F",
              "upColor": "#22ab94",
              "downColor": "#f7525f",
              "borderUpColor": "#22ab94",
              "borderDownColor": "#f7525f",
              "wickUpColor": "#22ab94",
              "wickDownColor": "#f7525f",
              "colorTheme": "light",
              "isTransparent": true,
              "locale": "en",
              "chartOnly": false,
              "scalePosition": "right",
              "scaleMode": "Normal",
              "fontFamily": "-apple-system, BlinkMacSystemFont, Trebuchet MS, Roboto, Ubuntu, sans-serif",
              "valuesTracking": "1",
              "changeMode": "price-and-percent",
              "symbols": [["FRED:VIXCLS|12M"]],
              "dateRanges": ["12m|1D", "60m|1W", "all|1M"],
              "fontSize": "10",
              "headerFontSize": "medium",
              "autosize": true,
              "width": "100%",
              "height": "100%",
              "noTimeScale": false,
              "hideDateRanges": false,
              "hideMarketStatus": false,
              "hideSymbolLogo": false
            }
            
            //  innerHTML  textContent -  LazyTradingViewWidget
            const configJson = JSON.stringify(vixConfig)
            script.innerHTML = configJson
            
            console.log(' FRED:VIXCLS Config:', vixConfig)
            console.log(' Config JSON:', configJson)
            
            // 
            const timeout = setTimeout(() => {
              console.warn(' VIX widget load timeout')
              this.error = true
              this.loading = false
              reject(new Error('Widget load timeout'))
            }, 8000)
            
            // 
            script.onload = () => {
              clearTimeout(timeout)
              console.log(' FRED:VIXCLS widget script loaded')
              this.loading = false
              resolve()
            }
            
            script.onerror = (error) => {
              clearTimeout(timeout)
              console.error(' VIX widget script failed:', error)
              this.error = true
              this.loading = false
              reject(new Error('Script load failed'))
            }
            
            //  DOM -  LazyTradingViewWidget 
            widgetContent.appendChild(script)
            container.appendChild(widgetContainer)
            
          } catch (error) {
            console.error(' VIX widget creation failed:', error)
            this.error = true
            this.loading = false
            reject(error)
          }
        })
      })
    }
  }
}
</script>

<style scoped>
.vix-widget-container {
  width: 100%;
  position: relative;
  border-radius: 8px;
  overflow: hidden;
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: #f8f9fa;
  color: #6c757d;
}

.loading-spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #e9ecef;
  border-top: 3px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  color: #721c24;
}

.retry-btn {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85rem;
}

.retry-btn:hover {
  background: #c82333;
}

:global(.vix-widget-container .tradingview-widget-container) {
  width: 100% !important;
  height: 100% !important;
  overflow: hidden !important; /*  LazyTradingViewWidget */
}

:global(.vix-widget-container .tradingview-widget-container__widget) {
  width: 100% !important;
  height: 100% !important;
}
</style>