  border-bottom: 1px solid #e9ecef;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  flex-shrink: 0;
  margin: 0;
}

.insight-full-widget .widget-header h4 {
  font-size: 0.95rem;
  font-weight: 600;
  color: #495057;
  margin: 0;
}

.insight-full-widget > :not(.widget-header) {
  flex: 1;
  min-height: 850px;
  height: 850px;
}

/* MFI Volume Profile content area */
.insight-full-widget.mfi-volume-profile > :not(.widget-header) {
  /*  */
  min-height: auto;
  height: auto;
}

.insight-widget {
  background: white; /*  */
  border: 1px solid #e0e0e0; /*  */
  border-radius: 8px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 600px; /*  500px  600px */
  height: auto; /*  auto  */
}

.insight-widget .widget-header {
  padding-bottom: 0.75rem;
  border-bottom: 1px solid #f0f0f0;
  margin-bottom: 1rem;
  flex-shrink: 0;
  height: auto; /*  auto  */
  display: flex;
  align-items: center;
}

.insight-widget .widget-header h4 {
  font-size: 0.95rem;
  font-weight: 600;
  color: #495057;
  margin: 0;
  line-height: 1.2;
}

.insight-widget > :not(.widget-header) {
  flex: 1;
  min-height: 550px; /*  */
  height: 550px; /*  */
  overflow: visible; /*  iframe  */
}

/* Symbol Insight  lazy-widget  8px padding */
.insight-widget .lazy-widget {
  padding: 8px;
  box-sizing: border-box;
}

.symbol-info .symbol {
  font-size: 1.5rem;
  font-weight: 700;
  color: #333;
  margin: 0 0 0.5rem 0;
}

.symbol-tags {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
}

.exchange-tag {
  font-size: 0.75rem;
  color: #007bff;
  background-color: #e7f3ff;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-weight: 500;
  border: 1px solid #90caf9;
  display: inline-block;
}

.industry-tag {
  font-size: 0.75rem;
  color: #666;
  background-color: #f5f5f5;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-weight: 500;
  border: 1px solid #e0e0e0;
  display: inline-block;
}

/*  */
.industry-tag.industry-tech-iot,
.industry-tag.industry-tech-satellite,
.industry-tag.industry-tech-software,
.industry-tag.industry-tech-hardware,
.industry-tag.industry-industrial-aerospace,
.industry-tag.industry-industrial-space,
.industry-tag.industry-communications,
.industry-tag.industry-automotive,
.industry-tag.industry-unknown,
.industry-tag.industry-other {
  background-color: #f5f5f5;
  color: #666;
  border-color: #e0e0e0;
}

/* Header Actions */
.header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.detail-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: linear-gradient(135deg, #6c757d, #5a6268);
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 0.85rem;
