  box-shadow: 0 2px 4px rgba(108, 117, 125, 0.2);
  line-height: 1;
  min-height: 36px;
}

.detail-btn:hover {
  background: linear-gradient(135deg, #5a6268, #495057);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(108, 117, 125, 0.3);
}

.btn-icon {
  font-size: 1rem;
  line-height: 1;
  display: inline-flex;
  align-items: center;
}

/* Widget Container - Match Market Overview Style */
.widget-container {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 2rem;
  overflow: hidden;
  position: relative;
}

.widget-container .widget-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem; /*  widget-container  header margin */
  padding-bottom: 0.75rem;
  border-bottom: 1px solid #f0f0f0;
}

.widget-container .widget-header h3 {
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin: 0;
}

/* Content Layout - Row-based Structure */
.content-layout {
  display: flex;
  flex-direction: column;
  gap: 0; /*  gap widget-container  margin-bottom  */
  margin-top: 0; /*  margin-top */
}

/*  */
@media (max-width: 1200px) {
  .widgets-container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .insight-widgets-container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .insight-full-widget {
    min-height: 800px;
    height: 800px;
  }
  
  .insight-full-widget.mfi-volume-profile {
    /*  */
    min-height: auto;
    height: auto;
  }
  
  .insight-full-widget > :not(.widget-header) {
    min-height: 750px;
    height: 750px;
  }
  
